<template>
  <div class="location">
    <div
      class="wordcloud"
      @mouseenter="intoWordcloud"
      @mousemove="mouseMove"
      @mouseleave="mouseLeave"
      ref="wordcloud"
    >
      <img src="../../assets/location.png" alt="" />
    </div>
    <pieechart class="echart" :pieData="pieData"></pieechart>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "location_word_cloud",
  data() {
    return {
      initWordcloud: "", // 进入braner初始位置
    };
  },
  computed: {
    ...mapState({
      pieData: (state) => state.location.pageData,
    }),
  },
  methods: {
    intoWordcloud(event) {
      this.initWordcloud = event.clientX;
      console.log(this.initWordcloud);
    },
    mouseMove(event) {
      let moveDistance = this.initWordcloud - event.clientX;
      this.$refs.wordcloud.style.transform = `translateX(${moveDistance}px)`;
    },
    // 鼠标离开
    mouseLeave() {
      this.$refs.wordcloud.style.transform = `translateX(0px)`;
    },
  },
  mounted() {
    this.$store.dispatch("location/sendLocationRequest", { page: 1, size: 50 });
  },
};
</script>

<style scoped>
.location {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  overflow: hidden;
}
.wordcloud {
  height: 200px;
  text-align: center;
}
.wordcloud > img {
  transform: translateX(-35%);
}
.wordcloud,
.echart {
  width: calc(100% - 10px);
  vertical-align: top;
  margin: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  padding: 10px;
  border-radius: 8px;
  box-sizing: border-box;
}
</style>